@import url('https://fonts.googleapis.com/css2?family=Caveat&family=Neonderthaw&family=Oswald&family=Sacramento&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Caveat&family=Neonderthaw&family=Oswald&family=Sacramento&display=swap');
body{
    background-color: white;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
header{
    height: 80px;
    min-width: 100%;
    border-radius: 10px;
    display: flex;
    justify-content: center;
}
.appname{
    position: relative;
    font-family: 'Caveat', cursive;
    color: white;
    top: 20px;
}
.container{
min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  border-radius: 30px 30px 0 0;
  align-items: center;
}
.add{
    background-color: rgb(131, 247, 127);
    width: 50px;
    height: 50px;
    position: fixed;
    border-radius: 50%;
    bottom: 40px;
    right: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.add1{
    background-color: rgb(204, 230, 255);
    width: 60px;
    height: 60px;
    position: absolute;
    border-radius: 50%;
    bottom: 65px;
}
.plus{
    color: white;
    font-size: large;
    cursor: pointer;
}
.box{
    background: rgba( 255, 255, 255, 0.25 );
  box-shadow: 0 8px 32px 0 rgba(131, 132, 148, 0.37);
  backdrop-filter: blur( 4px );
  -webkit-backdrop-filter: blur( 4px );
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.788);
  width: 200px;
  height: 270px;
  margin-top: 30px;
  text-align: center;
  overflow-wrap: break-word;
  }
h3{
    text-align: center;
    text-transform: capitalize;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.ui.button.green{
    padding: 10px;
    cursor: pointer;
    margin: 10px;
}
.main{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: absolute;
    top: 20%;
}
#option{
    background: #6bd46b;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    border: none;
    z-index: 100;
}
.button1{
    position: absolute;
    left: 0;
}
img{
    width: 20px;
    height: 20px;
  }
.arc{
    position: absolute;
    width: 100%;
    height: 80px;
    background-color: rgb(131, 247, 127);
    bottom: 0;
    z-index: -1;
    top: 0;
    border-radius: 0 0 30px 30px;
}
.hide{
    display: none;
}
.show{
    position: absolute;
    top: -30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    margin-top: 30px;
    text-align: center;
   overflow-wrap: break-word;
   background-color: white;
}
.normal{
    display: none;
}
.invert{
    transform: rotate(180deg);
    position: absolute;
    right: 10px;
    top: 10px;
}
.container1{
    display: flex;
    justify-content: center;
    background-color: rgb(255, 255, 255);
}
.poster{
    position: absolute;
    width: 100%;
    height: 100%;
    background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        top: 0;
        left: 0;
        z-index: -2;
}
.movietitle{
    position: absolute;
    width: 100%;
    bottom: 0;
    margin: 0;
color: rgb(43, 32, 32);
background: linear-gradient(rgba(255, 255, 255, 0),white);
}
.ui.button.blue{
    margin-top: 20px;
}
.green{
    color: #83F77F;
}
